
html,body,h1,h2,ul{margin:0;padding:0
}
body{font-size:0;background-color:#ececec
}
#app{color:#35495e;font-family:Source Sans Pro,Helvetica,sans-serif;font-size:16px
}
code{border-radius:2px;font-family:Menlo,Monaco,Consolas,Courier,monospace;font-size:.9em;-webkit-font-smoothing:initial;-moz-osx-font-smoothing:initial;word-break:break-all
}
code.z-code{color:#e96900;padding:3px 5px;margin:0 2px;background-color:#f8f8f8
}
.z-backtop-wrapper{color:#41b883
}
.heart{color:red
}
.clearfix{*zoom:1;clear:both
}
.clearfix:after{content:".";display:block;height:0;clear:both;visibility:hidden;zoom:1
}
h2{color:#41b883
}
a{color:#41b883;text-decoration:none
}
.header{text-align:center
}
.content{padding:20px 0;margin:50px 0;width:92%;background-color:#fff;border-radius:6px;box-sizing:border-box
}
.content .siderbar{width:16.66666667%;float:left;border-right:1px solid #eee
}
.content .siderbar .router-link-active{color:#41b883;border-right:2px solid #41b883;background-color:rgba(65,184,131,0.3)
}
.content .siderbar a{display:block
}
.content .siderbar>a{color:#35495e;height:42px;line-height:42px;padding:8px 16px 8px 32px
}
.content .siderbar>a .group-title{padding:0
}
.content .siderbar .group-title{display:inline-block;padding:8px 16px 8px 32px
}
.content .siderbar ul{list-style:none
}
.content .siderbar ul li a{color:gray;height:42px;line-height:42px;padding-left:48px;padding-right:16px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;font-size:14px
}
.content .siderbar ul li a:hover{color:#41b883
}
.content .section{padding:10px 4%;margin-left:16.66666667%;border-left:1px solid #eee
}
@media screen and (max-width:768px){
#app{font-size:14px
}
.content{width:100%;margin:10px 0
}
.content .siderbar{display:none
}
.content .section{margin-left:0;padding:10px
}
.z-navbar-wraper .z-navbar .small-logo{display:none
}
.z-navbar-wraper .z-navbar .z-guide{display:block
}
.z-navbar>ul{padding:10px
}
.z-backtop-wrapper.show-top{right:10px;bottom:10px
}
}
@media screen and (min-width:768px){
.main{text-align:center
}
.content{display:inline-block;text-align:left
}
.z-navbar>ul{padding:10px 60px
}
}
.footer{text-align:center;color:#fff;padding:50px 0;background-color:#727f80
}
.footer a{color:#fff
}
.logo{width:100px;height:100px
}
.introduce{margin:10px 5px 0 0;display:inline-block
}
.introduce:last-child{margin-right:0
}
.introduce-block{margin:10px 0 0
}
.z-navbar>ul{height:40px;line-height:40px
}
.z-navbar>ul>li{list-style:none;float:right;margin:0 .6em
}
.z-navbar>ul>li>a{color:#34495e;padding-bottom:3px
}
.z-navbar>ul>li>a:hover{border-bottom:3px solid #41b883
}
.z-navbar .small-logo{float:left;height:40px
}
.z-navbar .small-logo a{display:inline-block;height:40px
}
.z-navbar .small-logo a span{display:inline-block;height:100%;margin-left:10px;font-size:32px;float:right
}
.z-navbar .small-logo a:hover{border-bottom:none
}
.z-navbar .small-logo img{max-height:40px
}
.z-navbar .z-guide{float:left;position:relative;display:none
}
.z-navbar .z-guide .z-dropmenu-title>a{color:#41b883;font-weight:bold
}


